import Axios from "axios";
import React, { Component } from "react";
import { getUUID } from "../actions/fakturAction";
import FormKelolaUangBank from "./Component/FormKelolaUangBank";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import PrintNota from "../Stocking/Component/PrintNota";
import { warning } from "../Alert";

const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
  };
};
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class KelolaUangBank extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listKategori: [],
    };
  }
  componentDidMount() {
    this.props.dispatch(getUUID());
  }

  validasi(data) {
    if (data.tanggal === undefined) {
      warning("Mohon isi tanggal");
      return false;
    } else if (data.rekening === undefined) {
      warning("Mohon isi Rekening");
      return false;
    } else if (data.jumlah === undefined) {
      warning("Mohon isi Jumlah");
      return false;
    } else if (data.kategori === undefined) {
      warning("Mohon isi Kategori");
      return false;
    } else if (data.keperluan === undefined) {
      warning("Mohon isi Keperluan");
      return false;
    }
  }
  handleSubmitTambah(data) {
    if (data.tanggal === undefined) {
      warning("Mohon isi tanggal");
      return false;
    } else if (data.rekening === undefined) {
      warning("Mohon isi Rekening");
      return false;
    } else if (data.jumlah === undefined) {
      warning("Mohon isi Jumlah");
      return false;
    } else if (data.kategori === undefined) {
      warning("Mohon isi Kategori");
      return false;
    } else if (data.keperluan === undefined) {
      warning("Mohon isi Keperluan");
      return false;
    }
    const isi = {
      alatBayarTunai: [
        {
          tunaiKurs: 0,
          tunaiRp: 0,
          tunaiNetto: 0,
          tunaiTipe: "-",
        },
      ],
      alatBayarTrf: [
        {
          transferTanggalJanji: data.tanggal,
          transferRp: parseFloat(data.jumlah),
          transferTipe: "-",
          accFrom: data.rekening,
          accTo: "-",
          transferKurs: 0,
          transferNetto: parseFloat(data.jumlah),
        },
      ],
      noBon: this.props.noFaktur,
      tanggal: data.tanggal,
      noFaktur: this.props.noFaktur,
      tipeFaktur: "TAMBAH UANG BANK",
      keterangan: data.keperluan,
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(isi);
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    // for each ticket pass all its data into an array
    const ticketData = [
      "1",
      data.tanggal,
      parseFloat(data.jumlah).toLocaleString("id-ID"),
      data.rekening,
      parseFloat(data.jumlah).toLocaleString("id-ID"),
      data.keperluan,
    ];
    // push each tickcet's info into a row
    tableRows.push(ticketData);
    console.log(tableRows);
    var columTable = [
      "No",
      "Tanggal Janji",
      "Transfer nominal",
      "Nomor Rekening",
      "Transfer Netto",
      "Keperluan",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(serverNonMaster + "fico/dc/fico", isi)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Transaksi Berhasil",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() =>
            PrintNota(
              "Tanggal",
              data.tanggal,
              "",
              "",
              "Nomor Bon",
              this.props.noFaktur,
              "",
              "",
              "ADMIN",
              data.tanggal,
              "",
              columTable,
              "TAMBAH UANG BANK",
              tableRows
            )
          )
      )
      .catch(() =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text:
            "Sepertinya terjadi kesalahan, silahkan check koneksi anda dan ulangi lagi transaksi",
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }
  handleSubmitAmbil(data) {
    if (data.tanggal === undefined) {
      warning("Mohon isi tanggal");
      return false;
    } else if (data.rekening === undefined) {
      warning("Mohon isi Rekening");
      return false;
    } else if (data.jumlah === undefined) {
      warning("Mohon isi Jumlah");
      return false;
    } else if (data.kategori === undefined) {
      warning("Mohon isi Kategori");
      return false;
    } else if (data.keperluan === undefined) {
      warning("Mohon isi Keperluan");
      return false;
    }
    const isi = {
      alatBayarTunai: [
        {
          tunaiKurs: 0,
          tunaiRp: 0,
          tunaiNetto: 0,
          tunaiTipe: "-",
        },
      ],
      alatBayarTrf: [
        {
          transferTanggalJanji: data.tanggal,
          transferRp: parseFloat(data.jumlah),
          transferTipe: "-",
          accFrom: data.rekening,
          accTo: "-",
          transferKurs: 0,
          transferNetto: parseFloat(data.jumlah),
        },
      ],
      noBon: this.props.noFaktur,
      tanggal: data.tanggal,
      noFaktur: this.props.noFaktur,
      tipeFaktur: "AMBIL UANG BANK",
      keterangan: data.keperluan,
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(isi);
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    // for each ticket pass all its data into an array
    const ticketData = [
      "1",
      data.tanggal,
      parseFloat(data.jumlah).toLocaleString("id-ID"),
      data.rekening,
      parseFloat(data.jumlah).toLocaleString("id-ID"),
      data.keperluan,
    ];
    // push each tickcet's info into a row
    tableRows.push(ticketData);
    console.log(tableRows);
    var columTable = [
      "No",
      "Tanggal Janji",
      "Transfer nominal",
      "Nomor Rekening",
      "Transfer Netto",
      "Keperluan",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(serverNonMaster + "fico/dc/fico", isi)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Transaksi Berhasil",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() =>
            PrintNota(
              "Tanggal",
              data.tanggal,
              "",
              "",
              "Nomor Bon",
              this.props.noFaktur,
              "",
              "",
              "ADMIN",
              data.tanggal,
              "",
              columTable,
              "AMBIL UANG BANK",
              tableRows
            )
          )
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text:
            "Sepertinya terjadi kesalahan, silahkan check koneksi anda dan ulangi lagi transaksi",
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12 col-md-12">
          <div className="card">
            <div className="card-body">
              <div className="row">
                <div className="col-lg-12 col-md-12">
                  <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                    <li className="nav-item">
                      <a
                        href="#tambah-uang-bank"
                        data-toggle="tab"
                        aria-expanded="true"
                        className="nav-link active"
                      >
                        <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">
                          Tambah Uang Bank
                        </span>
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        href="#ambil-uang-bank"
                        data-toggle="tab"
                        aria-expanded="false"
                        className="nav-link "
                      >
                        <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">
                          Ambil Uang Bank
                        </span>
                      </a>
                    </li>
                  </ul>
                  <div className="tab-content">
                    <div className="tab-pane show active" id="tambah-uang-bank">
                      <FormKelolaUangBank
                        onSubmit={(data) => this.handleSubmitTambah(data)}
                        from="bank"
                      />
                    </div>
                    <div className="tab-pane " id="ambil-uang-bank">
                      <FormKelolaUangBank
                        onSubmit={(data) => this.handleSubmitAmbil(data)}
                        from="bank"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(KelolaUangBank);
