import Axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;
class FormKelolaUangBank extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listKategori: [],
      listRekening: [],
    };
  }

  componentDidMount() {
    Axios.get(server + "parameteruangs")
      .then((res) =>
        this.setState({
          listKategori: res.data,
        })
      )
      .catch((err) => console.log(err));
    Axios.get(server + "banks")
      .then((res) =>
        this.setState({
          listRekening: res.data,
        })
      )
      .catch((err) => console.log(err));
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4 col-lg-4">
            <Field
              name="tanggal"
              label="Tanggal"
              type="date"
              component={renderField}
            />
          </div>
          {this.props.from === "bank" ? (
            <div className="col-md-4 col-lg-4">
              <label htmlFor="">Rekening</label>
              <Field
                name="rekening"
                className="form-control"
                component="select"
              >
                <option value="DEFAULT">SILAHKAN PILIH</option>
                {this.state.listRekening.map((list) => (
                  <option value={list.noRekening} key={list.noRekening}>
                    {list.atasNama}
                  </option>
                ))}
              </Field>
            </div>
          ) : (
            <div className="col-md-4 col-lg-4"></div>
          )}
          <div className="col-md-4 col-lg-4"></div>
          <div className="col-lg-4 col-md-4">
            <label htmlFor="">Jumlah</label>
            <Field
              name="jumlah"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <label htmlFor="">Kategori</label>
            <Field name="kategori" className="form-control" component="select">
              <option value="DEFAULT">SILAHKAN PILIH</option>
              {this.state.listKategori.map((list) => (
                <option value={list.kodeParameter} key={list.kodeParameter}>
                  {list.kodeParameter}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="keperluan"
              label="Untuk Keperluan"
              type="text"
              component={renderField}
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormKelolaUangBank = reduxForm({
  form: "FormKelolaUangBank",
  enableReinitialize: true,
})(FormKelolaUangBank);
export default connect()(FormKelolaUangBank);
